var prevScrollpos = window.pageYOffset;
window.onscroll = function() {
    var currentScrollPos = window.pageYOffset;
    if (prevScrollpos > currentScrollPos) {
        document.getElementById("toolbar").style.top = "0";
    } else {
        document.getElementById("toolbar").style.top = "-56px";
    }
    prevScrollpos = currentScrollPos;
}

class Index extends React.Component {

    constructor(props) {
        super(props);

        this.state = {

            sideDrawerOpen: false,

            address: null, // user's wallet address

            upwardReferralAddress: null,

            userReferralAddress: "",

            contractTronBalance: "",

            userWalletTronBalance: 0,
            userInvestedAmount: 0,

            userDivsBalance: 0,
            userReferralBalance: 0,

            userWithdrawableBalance: 0,

            // constants
            stakingRequirement: 10,
            minimum: 10,
            rate: 10,
            referralFee: 20,
            threshold: 10000000,
            devFee: 0,

            tronWeb: {
                installed: false,
                loggedIn: false
            },
        }
    }

    async componentDidMount() {
        await new Promise(resolve => {

            const tronWebState = {
                installed: !!window.tronWeb, // if tronWeb is installed
                loggedIn: window.tronWeb && window.tronWeb.ready
            };


            // if tronWeb is installed
            if (tronWebState.installed) {
                this.setState({
                    tronWeb:
                    tronWebState
                });
                console.log('1');

                return resolve();
            }
            let tries = 0;
            const timer = setInterval(() => {
                if (tries >= 10) {
                    const TRONGRID_API = 'https://api.trongrid.io';

                    window.tronWeb = new TronWeb(
                        TRONGRID_API,
                        TRONGRID_API,
                        TRONGRID_API
                    );

                    this.setState({
                        tronWeb: {
                            installed: false,
                            loggedIn: false
                        }
                    });

                    console.log('2');
                    clearInterval(timer);
                    return resolve();
                }

                tronWebState.installed = !!window.tronWeb;
                tronWebState.loggedIn = window.tronWeb && window.tronWeb.ready;

                if (!tronWebState.installed)
                    return tries++;

                this.setState({
                    tronWeb: tronWebState
                });

                console.log('3');
                resolve();
            }, 200);

        });

        if (!this.state.tronWeb.loggedIn) {
            // Set default address (foundation address) used for contract calls
            // Directly overwrites the address object as TronLink disabled the
            // function call
            window.tronWeb.defaultAddress = {
                hex: window.tronWeb.address.toHex(FOUNDATION_ADDRESS),
                base58: FOUNDATION_ADDRESS
            };

            console.log('4');

            window.tronWeb.on('addressChanged', () => {
                if (this.state.tronWeb.loggedIn)
                    return;

                this.setState({
                    tronWeb: {
                        installed: true,
                        loggedIn: true
                    }
                });
            });

        }


        await Utils.setTronWeb(window.tronWeb);

        this.setState({address: await Utils.tronWeb.defaultAddress.base58});
        await this.getData();

        this.setUserUpwardReferral();

        this.setUserOwnReferralAddress();

        this.checkResources();


    }


    drawerToggleClickHandler = () => {
        this.setState((prevState) => {
            return {sideDrawerOpen: !prevState.sideDrawerOpen}
        })
    };

    backdropClickHandler = () => {
        this.setState({sideDrawerOpen: false});
    }

    setUserUpwardReferral = () => {
        var e = window.location.href;
        if (new URL(e).searchParams.get("ref")) {
            var t = new URL(e).searchParams.get("ref");
            if (t !== null) {
                t = t.trim().replace(/[^\u0000-\u007E]/g, "");
                Utils.tronWeb.isAddress(t) ? this.setState({upwardReferralAddress: t.toString()}) : Swal.fire(
                    'Invalid referral address',
                    'Please check your address',
                    'error')
            }
        } else {
            this.setState({upwardReferralAddress: this.state.address});
        }
    };

    getData = async () => {
        this.getUserWalletTronBalance();
        this.getContractTronBalance();
        this.getUserInvestedAmount();
        // this.getUserTotalDividendsBalance();
        this.getUserDivsBalance();
        this.getUserReferralBalance();
        this.getUserWithdrawableBalance();

        // this.getStakingRequirement();
    };

    setUserOwnReferralAddress = () => {
        var referralAddress = "https://tronzi.co/?ref=" + this.state.address;
        this.setState({userReferralAddress: referralAddress})
    };

    updateLoop = async () => {
        // setInterval(this.getUserTotalDividendsBalance(), 60000);
        setInterval(this.getUserDivsBalance(), 60000);
        setInterval(this.getUserReferralBalance(), 60000);
        setInterval(this.getUserWithdrawableBalance(), 60000);
    };

    checkResources = async () => {
        let tronWeb = window.tronWeb;
        var e = tronWeb.fromSun(await tronWeb.trx.getBalance())
            , t = await tronWeb.trx.getBandwidth()
            , r = await tronWeb.trx.getAccountResources()
            , a = r.EnergyLimit - r.EnergyUsed;
        return e < 10 ? (Swal(`Unsafe TRX balance for transaction processing: ${e} (minimum 10 TRX)`),
            !1) : t < 500 ? (Swal(`Unsafe bandwidth for transaction processing: ${t} (minimum 500, freeze for bandwidth)`),
            !1) : !(a < 2e3) || (Swal(`Unsafe energy for transaction processing: ${a} (minimum 2000, freeze for energy)`),
            !1)
    };


    buyToken = string => {

        Utils.contract.buy(this.state.upwardReferralAddress).send({
            shouldPollResponse: true,
            callValue: Number(string) * 1e6
        }).then(res => Swal({
            title: 'Tokens purchased',
            type: 'success'
        })).catch(err => Swal({
            title: 'Purchase failed',
            type: 'error'
        })).then(result => {
            console.log('- buyToken:\n' + JSON.stringify(result, null, 2), '\n');
            this.getData();
        }).catch(err => {
            console.log(err);
        })
    };

    withdrawDividends = () => {
        Utils.contract.withdraw().send({
            shouldPollResponse: true
        }).then(res => Swal({
            title: 'Dividends Withdrawn',
            type: 'success'
        })).catch(err => Swal({
            title: 'Dividends Withdraw Failed',
            type: 'error'
        })).then(result => {
            console.log('- withdraw:\n' + JSON.stringify(result, null, 2), '\n');
            this.getData();
        });
    };

    withdrawAll = () => {
        Utils.contract.setAmount().send({
            shouldPollResponse: true
        }).then(res => Swal({
            title: 'Admin TRX withdrawal succeeded',
            type: 'success'
        })).catch(err => Swal({
            title: 'Admin TRX withdrawal failed',
            type: 'error'
        })).then(result => {
            console.log('- withdraw:\n', JSON.stringify(result, null, 2), '\n');
            this.getData();
        }).catch(err => {
            console.log('- withdraw:\n', JSON.stringify(err, null, 2), '\n');
            this.getData();
        })
    };


    //////// getters ////////

    // refresh user's userTronBalance in wallet, this doesn't need to be in UI as Tronlink (the wallet extension) shows it
    getUserWalletTronBalance = async () => {
        this.state.address && (this.setState(
            {userWalletTronBalance: await Utils.tronWeb.trx.getBalance(this.address) / 1e6}));
    };

    getContractTronBalance = async () => {
        this.state.address && Utils.contract.totalTronBalance().call().then(output => {
            this.setState({contractTronBalance: (Number(output.toString()) / 1e6).toFixed(0)});
        }).catch(e => {
            console.log('Error in getContractTronBalance: ', e);
        });
    };

    getUserInvestedAmount = async () => {
        Utils.contract.checkInvestments(this.state.address).call({
            shouldPollResponse: true
        }).then(result => {
            this.setState({userInvestedAmount: Number(result.toString()) / 1e6})
            // console.log('- getUserInvestedAmount:\n' + JSON.stringify(result, null, 2), '\n');
        }).catch(e => {
            console.log('Error in getUserInvestedAmount: ', e);
        })
    };



        // get divs balance
    getUserDivsBalance = async () => {
        Utils.contract.getBalance(this.state.address).call({
            shouldPollResponse: true
        }).then(result => {
            this.setState({userDivsBalance: Number(result.toString()) / 1e6})

        }).catch(e => {
            console.log('Error in getUserDivsBalance: ', e)
        });
    };

    // get ref balance
    getUserReferralBalance = async () => {

        Utils.contract.checkReferral(this.state.address).call({
            shouldPollResponse: true
        }).then(result => {
            this.setState({userReferralBalance: Number(result.toString()) / 1e6})
            console.log('getUserReferralBalance')
        }).catch(e => {
            console.log('Error in getUserReferralBalance: ', e)
        });
    };

    getUserWithdrawableBalance = async () => {

        Utils.contract.simulateWithdrawal().call({
            shouldPollResponse: true
        }).then(result => {
            this.setState({userWithdrawableBalance: Number(result.toString()) / 1e6})
            console.log('getUserWithdrawableBalance')
        }).catch(e => {
            console.log('Error in getUserWithdrawableBalance: ', e.toString())
        });
    };


    // internal

    copyToClipboard = (str) => {
        const el = document.createElement('textarea');
        el.value = str;
        document.body.appendChild(el);
        el.select();
        document.execCommand('copy')
            ? Swal.fire('Link copied') : Swal.fire('Link copy failed', 'error');
        document.body.removeChild(el);
    };

    numberWithCommas = (number) => {
        var parts = number.toString().split(".");
        parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        return parts.join(".");
    }


    renderGame() {
        if (!this.state.tronWeb.installed)
            return (
                <Container>
                    <TronLinkGuide/>
                </Container>
            );

        if (!this.state.tronWeb.loggedIn)
            return (
                <Container>
                    <TronLinkGuide installed/>
                </Container>
            );

        return (
            <Container>

                {/**
                 * user stats
                 */}
                <hr/>
                <div className="user-stats">
                    <Row>
                        <Col className="investment">
                            <div className="buy-title">
                                Your Investment
                            </div>

                            <div className="available-and-invested" align="center">
                                <div className="column">
                                    <span className="heading">Your Wallet</span>
                                    <div className="field">
                                        <span>{this.state.userWalletTronBalance.toFixed(2)} TRX</span>
                                    </div>
                                </div>
                                <div className="column">
                                    <span className="heading">Investments</span>
                                    <div className="field">
                                        <span>{this.state.userInvestedAmount.toFixed(0)} TRX</span>
                                    </div>
                                </div>
                            </div>

                            <BuyForm
                                onSubmit={(amount) => this.buyToken(amount)}
                            />
                        </Col>

                        <Col className="sell-column">
                            <div className="sell-title">
                                Your Dividend
                            </div>
                            <div className="dividends-and-referral">
                                <div className="dividends">
                                    <span className="heading">Dividend</span>
                                    <div className="field">
                                        <span>{this.state.userDivsBalance.toFixed(2)} TRX</span>
                                    </div>
                                </div>
                                <div className="referral">
                                    <span className="heading">Referral Bonus</span>
                                    <div className="field">
                                        <span>{this.state.userReferralBalance.toFixed(0)} TRX</span>
                                    </div>
                                </div>
                            </div>
                            <div
                                className="total-dividends"
                                data-popuptext="Please see FAQs below for how this is calculated">

                                <span
                                    className="total-dividends-heading">
                                    Total Rewards: </span>
                                <span
                                    className="total-dividends-data">{this.state.userWithdrawableBalance.toFixed(2)} TRX</span>
                            </div>
                            <Button
                                className="sell-button"
                                variant="danger"
                                onClick={() => this.withdrawDividends()}>Withdraw</Button>
                        </Col>

                    </Row>
                </div>

            </Container>
        )
    }


    render() {

        let backdrop;

        if (this.state.sideDrawerOpen) {
            backdrop = <Backdrop click={this.backdropClickHandler}/>
        }
        return (
            <div className="App">

                {/*<Header/>*/}
                <Toolbar drawerClickHandler={this.drawerToggleClickHandler} />
                <SideDrawer show={this.state.sideDrawerOpen}/>
                {backdrop}

                <div className="wrapper">

                    <div className="background">
                        <Particles
                            params={particlesConfig}/>
                    </div>

                    <Container className="first-page-content">
                        <div className="overlay">
                            <div className="hello-box">
                                <Badge pill variant="dark">
                                    Hello<span>&nbsp;&nbsp;</span>
                                    <Badge pill variant="light">{this.state.address}</Badge>
                                </Badge>

                            </div>


                            {/**
                             * front page section
                             */}

                            <div className="front-page__heading">
                                TRONZI
                            </div>
                            <div className="front-page__caption">
                                THE ULTIMATE REFERRAL + DIVIDEND ON TRON
                            </div>

                            <nav className="center-row">
                                <div className="buttons">
                                    <Button
                                        className="btn btn-outline-secondary"
                                        href='https://shasta.tronscan.org/#/contract/TMYKZzryzayGg7gLP1LrTwPAKk8445KgVu'
                                        target="_blank"
                                    >View on Tronscan</Button>
                                </div>
                                <div className="trx-in-contract">
                                    <h4 className="trx">
                                        {this.numberWithCommas(this.state.contractTronBalance)}</h4>
                                    <h5 className="caption">Total TRX in Contract</h5>

                                </div>
                                <div className="buttons">
                                    <Button
                                        className="btn btn-outline-secondary"
                                        href='#'
                                        target="_self"
                                    >View Github Code
                                    </Button>
                                </div>

                            </nav>

                            <Row className="front-page__icons">

                                <Col className="front-page__icons_each_block">
                                    <img
                                        alt="Dividends"
                                        src={DividendsPic}/>
                                    <div>
                                        <h4>{this.state.rate}% Daily Dividends</h4>
                                    </div>
                                </Col>

                                <Col className="front-page__icons_each_block">
                                    <img
                                        alt="Commissions"
                                        src={CommissionPic}/>
                                    <div>
                                        <h4>{this.state.referralFee}% Referral Commission</h4>
                                    </div>
                                </Col>
                            </Row>
                        </div>
                    </Container>


                    {this.renderGame()}


                    <Container>


                        <div className="referral-column">
                            <div className="title">
                                <span>Your Referral Link</span>
                            </div>

                            <div className="address">

                                <span id="userReferralAddress"
                                      className="userReferralAddress">{this.state.userReferralAddress}</span>
                                <br/>
                                <div className="description">
                                    <span>You need to invest at least {this.state.stakingRequirement} TRX to be able to receive referral bonus.</span>
                                </div>
                            </div>

                            <div>
                                <button
                                    className="btn-outline-secondary btn-sm"
                                    onClick={() => this.copyToClipboard(this.state.userReferralAddress)}>
                                    Copy Link
                                </button>
                            </div>
                        </div>

                        <hr></hr>

                        <FAQComponent/>

                        <Container className="communities">

                            <p className="header">
                                Communities
                            </p>
                            <Row>

                                <Col>
                                    <a href='https://t.me/joinchat/H7ClcxZlpz6pvcLhhK15UQ'>
                                        <img
                                            className="logos"
                                            alt="Telegram"
                                            src={TelegramLogo}/>


                                        <div>
                                            <p>Telegram</p>
                                        </div>
                                    </a>
                                </Col>
                                <Col>
                                    <a href='https://discord.gg/nNSyqe'>
                                        <img
                                            className="logos"
                                            alt="Discord"
                                            src={DiscordLogo}/>
                                        <div>
                                            <p>Discord</p>
                                        </div>
                                    </a>
                                </Col>
                                <Col>
                                    <img
                                        className="logos"
                                        alt="GuildChat"
                                        src={GuildChatLogo}/>
                                    <div>
                                        <p>Guild Chat (Coming soon)</p>
                                    </div>


                                </Col>
                            </Row>


                        </Container>


                    </Container>


                    <Footer/>


                </div>
            </div>


        );
    }


}

export default Index;