import React, {Component} from 'react';
import Card from 'react-bootstrap/Card'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

import './FAQComponent.scss';


import {Collapse} from 'react-collapse';


class FAQComponent extends Component {

    constructor() {
        super();

        // Initial state
        this.state = {
            Q1: false,
            Q2: false,
            Q3: false,
            Q4: false,
        };

    }

    toggle1() {
        this.setState({
            Q1: !this.state.Q1
        });
    }
    toggle2() {
        this.setState({
            Q2: !this.state.Q2
        });
    }

    toggle3() {
        this.setState({
            Q3: !this.state.Q3
        });
    }
    toggle4() {
        this.setState({
            Q4: !this.state.Q4
        });
    }


    render() {
        return (
            <div className="main">
                <div className="faq-and-fees--headings">
                    <h2 className="FAQ">FAQs</h2>
                    <h2 className="fees">Fees</h2>
                </div>

            <Row>
                <Col className="col-sm-12 col-lg-6">
                    <div>
                        <Card>
                            <div className="card-header"
                                 onClick={() => this.toggle1()}>
                                <h5 className="mb-0">
                                    <button
                                        className="btn btn-link text-dark"
                                        >What is this game?
                                    </button>
                                </h5>
                            </div>

                            <Collapse isOpened={this.state.Q1}>
                                <div className="card-body text-left">
                                    The revenue in this game is generated through newcomers, the earlier you get in, the
                                    more profit you stand to gain. You may earn many times your investment or you may lose your
                                    investment. Invest with caution.
                                </div>
                            </Collapse>
                        </Card>
                        <Card>
                            <div className="card-header"
                                 onClick={() => this.toggle2()}>
                                <h5 className="mb-0 text-muted">
                                    <button
                                        className="btn btn-link text-dark"
                                        >How do I ensure that this is legit?
                                    </button>
                                </h5>
                            </div>

                            <Collapse isOpened={this.state.Q2}>
                                <div className="card-body text-left">
                                    The whole game worked off of a smart contract that is stored on the
                                    TRON blockchain.
                                    <a
                                    href='https://shasta.tronscan.org/#/contract/TMYKZzryzayGg7gLP1LrTwPAKk8445KgVu'
                                    rel='noopener noreferrer'
                                    target='_blank'>This contract</a> handles
                                    all the gaming mechanics: receiving your coins, calculating your dividends,
                                    sending you dividends, etc.
                                </div>
                            </Collapse>
                        </Card>

                    </div>
                </Col>
                <Col className="col-sm-12 col-lg-6">

                    <Card>
                        <div className="card-header"
                             onClick={() => this.toggle3()}>
                            <h5 className="mb-0">
                                <button className="btn btn-link text-dark">
                                    What happens when I buy?
                                </button>
                            </h5>
                        </div>

                        <Collapse isOpened={this.state.Q3}>
                            <div className="card-body text-left">
                                10% will go to Zoom Studio to support the development of future games.
                                70% of your investment becomes your principal and starts generating dividend for you.
                                (Note that original principal is not withdrawable)
                                20% of your investment goes to your referral if you have one.
                                If you don’t have a referral, then that 20% will contribute to the global treasury.

                            </div>
                        </Collapse>
                    </Card>
                    <Card >
                        <div className="card-header"
                             onClick={() => this.toggle4()}>
                            <h5 className="mb-0">
                                <button className="btn btn-link text-dark">
                                    What happens when I sell?
                                </button>
                            </h5>
                        </div>

                        <Collapse isOpened={this.state.Q4}>
                            <div className="card-body text-left">
                                When the contract balance is smaller than 10 million, you might find you withdrawable
                                dividend to be smaller than your dividend balance + referral balance. This is because
                                we have set up the system to charge a withdrawal fee on your dividends (not including
                                your referral bonus) when the amount of TRX in the
                                contract is below 10 million to support the longevity of this contract. The formula
                                to calculate your withdrawal fee is:
                                <br/>
                                ( (your dividend balance ^ 2) / total TRX in contract ) * 3
                                <br/>
                                This mechanism is here to keep the pot upward of 10 million TRX.


                            </div>
                        </Collapse>
                    </Card>

                </Col>
            </Row>
            </div>

        )
    }
}

export default FAQComponent;
